<template>
  <div class="container">
    <h3>Прибыль/посещаемость</h3>
    <div>
      <p>
        Начальная дата:
      </p>
      <date-picker v-model="dateFrom" valueType="date"></date-picker>
      <p>
        Конечная дата:
      </p>
      <date-picker v-model="dateTo" valueType="date"></date-picker>
    </div>
    <canvas ref="mainChart"></canvas>
  </div>
</template>

<script>
import Chart from 'chart.js';
import DatePicker from 'vue2-datepicker';
import 'vue2-datepicker/index.css';

export default {
  name: 'Analytics-test',
  components: {
    DatePicker
  },
  data: () => ({
    dateFrom: new Date('2021-11-01'),
    dateTo: new Date(),
    flagStartDate: false,
    chartData: null,
    groupBy: 'month',
    mychart:null,
    labels: [],
    dataset: {},
    draftData: null,
    data: [],
    datacollection: [],
    clubsId: ['5c3c5e12ba86198828baa4a7', '5c3c5e20ba86198828baa4c5', '60353d6edbb58a135bf41856', '61e9995d4ec0f29dc8447f81', '61e999fc4ec0f29dc844835e'],
    // clubsId: ['5c3c5e12ba86198828baa4a7', '61e999fc4ec0f29dc844835e'],
    // clubsId: ['61e999fc4ec0f29dc844835e'],
  }),
  methods: {
    draw() {
      if (this.mychart) {
        this.mychart.destroy();
      }
      const ctx = this.$refs.mainChart;
      this.mychart = new Chart(ctx,
        {
          type: 'line',
          data: {
            labels: this.labels,
            datasets: this.datacollection
          },
          options: {
            legend: {
              display: true,
              position: 'bottom',
            },
            responsive: true,
            elements: {
            },
            scales: {
              xAxes: [{
                type: "time",
                display: true,
                scaleLabel: {
                  display: false,
                },
                ticks: {
                  major: {
                    fontStyle: "bold",
                    fontColor: "#FF0000"
                  }
                }
              }],

              yAxes: [
                {
                  id: 'y1',
                  type: 'linear',
                  position: 'left',
                  display: false
                },
                {
                  id: 'y2',
                  type: 'linear',
                  position: 'right',
                  display: false
                },
                {
                  id: 'y3',
                  type: 'linear',
                  position: 'left',
                  display: false
                },
                {
                  id: 'y4',
                  type: 'linear',
                  position: 'right',
                  display: false
                },
                {
                  id: 'y5',
                  type: 'linear',
                  position: 'left',
                  display: false
                },

                {
                  display: false,
                  gridLines: {
                    display: false
                  },
                  scaleLabel: {
                    display: true,
                    labelString: this.labelY
                  },
                  ticks: {
                    min: 0, 
                    beginAtZero: true,
                    stepSize: 100  
                  }
                }]
            }
          }

        });
    },

    // Выручка

    async clearData() {
      console.log('start clearing')
      this.datacollection = [];
      this.labels = [];
      this.mychart.data.datasets = []
      this.mychart.data.labels = []
    },

    incomingsClub(clubId) {
      switch (clubId) {
        case '5c3c5e12ba86198828baa4a7':
          return { label: "Выручка Фрунзенской", borderColor: "#3e95cd", fill: false }
        case '5c3c5e20ba86198828baa4c5':
          return { label: "Выручка Чернышевской", borderColor: "#8e5ea2", fill: false };
        case '60353d6edbb58a135bf41856':
          return { label: "Выручка Василеостровской", borderColor: "#e8c3b9", fill: false };
        case '61e9995d4ec0f29dc8447f81':
          return { label: "Выручка Московской", borderColor: "#3cba9f", fill: false };
        case '61e999fc4ec0f29dc844835e':
          return { label: "Выручка Лесной", borderColor: "#c45850", fill: false };
        case 'all':
          return { label: "Выручка сети", borderColor: "#8e8786", fill: false };
        default:
          return 'Неизвестный клуб';
      }
    },

    async loadIncomings(clubsId) {
      try {
        for (let clubId in clubsId) {
          clubId = clubsId[clubId]
          let dateFrom = this.dateFrom
          let dateTo = this.dateTo
          let groupBy = this.groupBy
          let potential = true
          let definitely = true
          await this.$store.dispatch('loadIncomings', { clubId, dateFrom, dateTo, groupBy, potential, definitely })
          this.draftData = this.$store.state.incomings
          if (this.labels.length === 0) {
            this.getDates()
          }
          this.flagStartDate = true
          await this.getIncomings(clubId)
          this.flagStartDate = false
        }
        this.getIncomingsTotal()
      } catch (e) {
        console.error(e)
      }
    },

    getDates() {
      for (let item in this.draftData) {
        if (item === 'items') {
          for (let elem in this.draftData[item]) {
            this.labels.push(this.draftData[item][elem].date.slice(0, 7))
          }
        }
      }
    },

    bindDataDates(indexDate) {
      return Array(indexDate).fill(null);
    },

    getIncomings(clubId) {
      for (let item in this.draftData) {
        if (item === 'items') {
          for (let elem in this.draftData[item]) {
            let positionDate = this.labels.indexOf(this.draftData[item][elem].date.slice(0, 7))
            if (this.flagStartDate && positionDate > 0) {
              let zerroArray = this.bindDataDates(positionDate)
              this.data = this.data.concat(zerroArray)
            }
            this.data.push(this.draftData[item][elem].amount)
            this.flagStartDate = false
          }
          this.dataset.data = this.data
          Object.assign(this.dataset, this.incomingsClub(clubId))
          Object.assign(this.dataset, { yAxisID: 'y1' })
          this.datacollection.push(this.dataset)
          this.data = []
          this.dataset = {}
        }
      }
    },

    getIncomingsTotal() {
      for (let item in this.datacollection) {

        if (!this.data.length) {
          this.data = this.datacollection[item].data
          continue
        }
        const firstArr = this.data
        const secondArr = this.datacollection[item].data;
        this.data = []
        let length;

        if (firstArr.length >= secondArr.length) {
          length = firstArr.length;
        } else {
          length = secondArr.length;
        }
        for (let i = 0; i < length; i++) {
          const a = firstArr[i] === undefined ? 0 : firstArr[i];
          const b = secondArr[i] === undefined ? 0 : secondArr[i];
          this.data.push(a + b);
        }
      }
      this.dataset.data = this.data
      Object.assign(this.dataset, this.incomingsClub('all'))
      Object.assign(this.dataset, { yAxisID: 'y1' })
      this.datacollection.push(this.dataset)
      this.data = []
      this.dataset = {}
    },

    // Средняя сумма за жизнь
    avgIncomingsClub(clubId) {
      switch (clubId) {
        case '5c3c5e12ba86198828baa4a7':
          return { label: "Средняя сумма за жизнь Фрунзенской", borderColor: "#3e95cd", fill: false }
        case '5c3c5e20ba86198828baa4c5':
          return { label: "Средняя сумма за жизнь Чернышевской", borderColor: "#8e5ea2", fill: false };
        case '60353d6edbb58a135bf41856':
          return { label: "Средняя сумма за жизнь Василеостровской", borderColor: "#e8c3b9", fill: false };
        case '61e9995d4ec0f29dc8447f81':
          return { label: "Средняя сумма за жизнь Московской", borderColor: "#3cba9f", fill: false };
        case '61e999fc4ec0f29dc844835e':
          return { label: "Средняя сумма за жизнь Лесной", borderColor: "#c45850", fill: false };
        default:
          return 'Неизвестный клуб';
      }
    },

    getDatesAvgIncome() {
      for (let item in this.draftData) {
        this.labels.push(this.draftData[item].date.slice(0, 7))
      }
    },

    async loadAvgIncomings(clubsId) {
      try {
        for (let clubId in clubsId) {
          clubId = clubsId[clubId]
          let dateFrom = this.dateFrom
          let dateTo = this.dateTo
          let groupBy = this.groupBy
          await this.$store.dispatch('loadAvgIncome', { clubId, dateFrom, dateTo, groupBy })
          this.draftData = this.$store.state.avgIncome
          if (this.labels.length === 0) {
            this.getDatesAvgIncome()
          }
          this.flagStartDate = true
          await this.getAvgIncomings(clubId)
          this.flagStartDate = false
        }
      } catch (e) {
        console.error(e)
      }
    },

    getAvgIncomings(clubId) {
      for (let item in this.draftData) {
        let positionDate = this.labels.indexOf(this.draftData[item].date.slice(0, 7))
        if (this.flagStartDate && positionDate > 0) {
          let zerroArray = this.bindDataDates(positionDate)
          this.data = this.data.concat(zerroArray)
        }
        this.data.push(this.draftData[item].avgIncome)
        this.flagStartDate = false
      }
      this.dataset.data = this.data
      Object.assign(this.dataset, this.avgIncomingsClub(clubId))
      Object.assign(this.dataset, { yAxisID: 'y2', hidden: true })
      this.datacollection.push(this.dataset)
      this.data = []
      this.dataset = {}
    },

    // Среднее кол-во оплат

    avgPaymentsClub(clubId) {
      switch (clubId) {
        case '5c3c5e12ba86198828baa4a7':
          return { label: "Среднее кол-во оплат Фрунзенской", borderColor: "#3e95cd", fill: false }
        case '5c3c5e20ba86198828baa4c5':
          return { label: "Среднее кол-во оплат Чернышевской", borderColor: "#8e5ea2", fill: false };
        case '60353d6edbb58a135bf41856':
          return { label: "Среднее кол-во оплат Василеостровской", borderColor: "#e8c3b9", fill: false };
        case '61e9995d4ec0f29dc8447f81':
          return { label: "Среднее кол-во оплат Московской", borderColor: "#3cba9f", fill: false };
        case '61e999fc4ec0f29dc844835e':
          return { label: "Среднее кол-во оплат Лесной", borderColor: "#c45850", fill: false };
        default:
          return 'Неизвестный клуб';
      }
    },

    async loadAvgPayments(clubsId) {
      try {
        for (let clubId in clubsId) {
          clubId = clubsId[clubId]
          let dateFrom = this.dateFrom
          let dateTo = this.dateTo
          let groupBy = this.groupBy
          await this.$store.dispatch('loadAvgIncome', { clubId, dateFrom, dateTo, groupBy })
          this.draftData = this.$store.state.avgIncome
          if (this.labels.length === 0) {
            this.getDatesAvgIncome()
          }
          this.flagStartDate = true
          await this.getAvgPayments(clubId)
          this.flagStartDate = false
        }
      } catch (e) {
        console.error(e)
      }
    },

    getAvgPayments(clubId) {
      for (let item in this.draftData) {
        let positionDate = this.labels.indexOf(this.draftData[item].date.slice(0, 7))
        if (this.flagStartDate && positionDate > 0) {
          let zerroArray = this.bindDataDates(positionDate)
          this.data = this.data.concat(zerroArray)
        }
        this.data.push(this.draftData[item].avgPayments)
        this.flagStartDate = false
      }
      this.dataset.data = this.data
      Object.assign(this.dataset, this.avgPaymentsClub(clubId))
      Object.assign(this.dataset, { yAxisID: 'y3', hidden: true })
      this.datacollection.push(this.dataset)
      this.data = []
      this.dataset = {}

    },

    // Посещаемость

    avgAttendanceClub(clubId) {
      switch (clubId) {
        case '5c3c5e12ba86198828baa4a7':
          return { label: "Посещаемость Фрунзенской", borderColor: "#3e95cd", fill: false }
        case '5c3c5e20ba86198828baa4c5':
          return { label: "Посещаемость Чернышевской", borderColor: "#8e5ea2", fill: false };
        case '60353d6edbb58a135bf41856':
          return { label: "Посещаемость Василеостровской", borderColor: "#e8c3b9", fill: false };
        case '61e9995d4ec0f29dc8447f81':
          return { label: "Посещаемость Московской", borderColor: "#3cba9f", fill: false };
        case '61e999fc4ec0f29dc844835e':
          return { label: "Посещаемость Лесной", borderColor: "#c45850", fill: false };
        default:
          return 'Неизвестный клуб';
      }
    },

    async loadAvgSchedule(clubsId) {
      try {
        for (let clubId in clubsId) {
          clubId = clubsId[clubId]
          let dateFrom = this.dateFrom
          let dateTo = this.dateTo
          let groupBy = this.groupBy
          await this.$store.dispatch('loadAvgSchedule', { clubId, dateFrom, dateTo, groupBy })
          this.draftData = this.$store.state.avgShedule
          if (this.labels.length === 0) {
            this.getDatesAvgIncome()
          }
          this.flagStartDate = true
          await this.getAvgAttendance(clubId)
          this.flagStartDate = false
        }
      } catch (e) {
        console.error(e)
      }
    },

    getAvgAttendance(clubId) {
      for (let item in this.draftData) {
        let positionDate = this.labels.indexOf(this.draftData[item].date.slice(0, 7))
        if (this.flagStartDate && positionDate > 0) {
          let zerroArray = this.bindDataDates(positionDate)
          this.data = this.data.concat(zerroArray)
        }
        this.data.push(this.draftData[item].avg)
        this.flagStartDate = false
      }
      this.dataset.data = this.data
      Object.assign(this.dataset, this.avgAttendanceClub(clubId))
      Object.assign(this.dataset, { yAxisID: 'y4', hidden: true })
      this.datacollection.push(this.dataset)
      this.data = []
      this.dataset = {}
    },
    // Тренировок

    participantsCountClub(clubId) {
      switch (clubId) {
        case '5c3c5e12ba86198828baa4a7':
          return { label: "Тренировок на Фрунзенской", borderColor: "#3e95cd", fill: false }
        case '5c3c5e20ba86198828baa4c5':
          return { label: "Тренировок на Чернышевской", borderColor: "#8e5ea2", fill: false };
        case '60353d6edbb58a135bf41856':
          return { label: "Тренировок на Василеостровской", borderColor: "#e8c3b9", fill: false };
        case '61e9995d4ec0f29dc8447f81':
          return { label: "Тренировок на Московской", borderColor: "#3cba9f", fill: false };
        case '61e999fc4ec0f29dc844835e':
          return { label: "Тренировок на Лесной", borderColor: "#c45850", fill: false };
        default:
          return 'Неизвестный клуб';
      }
    },

    async loadTrainings(clubsId) {
      try {
        for (let clubId in clubsId) {
          clubId = clubsId[clubId]
          let dateFrom = this.dateFrom
          let dateTo = this.dateTo
          let groupBy = this.groupBy
          await this.$store.dispatch('loadAvgSchedule', { clubId, dateFrom, dateTo, groupBy })
          this.draftData = this.$store.state.avgShedule
          if (this.labels.length === 0) {
            this.getDatesAvgIncome()
          }
          this.flagStartDate = true
          await this.getParticipantsCount(clubId)
          this.flagStartDate = false
        }
      } catch (e) {
        console.error(e)
      }
    },

    async getParticipantsCount(clubId) {
      for (let item in this.draftData) {
        let positionDate = this.labels.indexOf(this.draftData[item].date.slice(0, 7))
        if (this.flagStartDate && positionDate > 0) {
          let zerroArray = await this.bindDataDates(positionDate)
          this.data = this.data.concat(zerroArray)
        }
        this.data.push(this.draftData[item].participantsCount)
        this.flagStartDate = false
      }
      this.dataset.data = this.data
      Object.assign(this.dataset, this.participantsCountClub(clubId))
      Object.assign(this.dataset, { yAxisID: 'y5', hidden: true })
      this.datacollection.push(this.dataset)
      this.data = []
      this.dataset = {}
    },

    async updateCharts() {
      await this.clearData(),
      await this.loadIncomings(this.clubsId),
      await this.loadAvgIncomings(this.clubsId),
      await this.loadAvgPayments(this.clubsId),
      await this.loadAvgSchedule(this.clubsId),
      await this.loadTrainings(this.clubsId)
      this.$nextTick(() => {
        this.mychart.data.datasets = this.datacollection
        this.mychart.data.labels = this.labels
        this.mychart.update()
      })
    }
  },

  watch: {
    dateFrom() {
      this.updateCharts()
    },
    dateTo() {
      this.updateCharts()
    }
  },

  async mounted() {
    await this.loadIncomings(this.clubsId)
    await this.loadAvgIncomings(this.clubsId),
    await this.loadAvgPayments(this.clubsId),
    await this.loadAvgSchedule(this.clubsId),
    await this.loadTrainings(this.clubsId)
    this.$nextTick(() => {
      this.draw()
    })
  },

  created() {
    // 5c3c5e12ba86198828baa4a7 - фрунза
    // 5c3c5e20ba86198828baa4c5 - черн
    // 60353d6edbb58a135bf41856 - васька
    // 61e9995d4ec0f29dc8447f81 - моск
    // 61e999fc4ec0f29dc844835e - лесн
    // let clubId = '5c3c5e12ba86198828baa4a7'
    // let dateFrom = '2021-06-01T00:00:00'
    // let dateTo = '2022-07-02T23:59:59'
    // let groupBy = 'month'
    // this.$store.dispatch('loadTrainings')
    // this.$store.dispatch('loadMetro')
    // this.$store.dispatch('loadIncomings', {clubId, dateFrom, dateTo, groupBy})
    // this.$store.dispatch('loadSources', {clubId, dateFrom, dateTo})
    // this.$store.dispatch('loadConversion', {clubId, dateFrom, dateTo, groupBy})
    // this.$store.dispatch('loadAvgSchedule', {clubId, dateFrom, dateTo, groupBy})
    // this.$store.dispatch('loadAvgIncome', {clubId, dateFrom, dateTo, groupBy})
  }
}
</script>

<style>
.container form {
  display: flex;
}
</style>