import Vue from 'vue';
import Vuex from 'vuex';
import axios from 'axios';
import { API_BASE_URL } from '../config';

Vue.use(Vuex);

export default new Vuex.Store({
  state:{
    trainings: [],
    incomings: [],
    sources: [],
    avgShedule: [],
    metro: [],
    conversion: [],
    avgIncome: [],
    status: '',
    token: localStorage.getItem('token') || '',
    user : {},
  },
  mutations:{
    UpdateTrainingsInfo(state, trainings) {
      state.trainings = trainings;
    },
    UpdateIncomingsInfo(state, incomings) {
      state.incomings = incomings;
    },
    UpdateSourcesInfo(state, sources) {
      state.sources = sources;
    },
    UpdateAvgScheduleInfo(state, avgShedule) {
      state.avgShedule = avgShedule;
    },
    UpdateMetroInfo(state, metro) {
      state.metro = metro;
    },
    UpdateConversionInfo(state, conversion) {
      state.conversion = conversion;
    },
    UpdateAvgIncomeInfo(state, avgIncome) {
      state.avgIncome = avgIncome;
    },
    auth_request(state){
      state.status = 'loading'
    },
    auth_success(state, token, user){
      state.status = 'success'
      state.token = token
      state.user = user
    },
    auth_error(state){
      state.status = 'error'
    },
    logout(state){
      state.status = ''
      state.token = ''
    },
  },
  getters:{
    isLoggedIn: state => !!state.token,
    authStatus: state => state.status,
  },
  actions:{
    async loadTrainings(context){
      return await axios
      .post(`${API_BASE_URL}/analytics.trainings`,
      {},
      { params: {
        token: context.state.token
        }
      ,
        headers: 
        {"Content-Type": "application/x-www-form-urlencoded"}
       },
      )
      .then((response) => {
        context.commit('UpdateTrainingsInfo',response.data)
      })
      .catch(function(error) {
          console.log(error.response)
      })
      },
    async loadIncomings(context, payload){
      return await axios.post(
        `${API_BASE_URL}/analytics.incomings`,
        {},
        {
          params: {
            token: context.state.token,
            club: payload.clubId,
            dateStarts: payload.dateFrom,
            dateEnds: payload.dateTo,
            groupBy: payload.groupBy,
          },
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        },
      )
      .then((response) => {
        context.commit('UpdateIncomingsInfo',response.data)
      })
      .catch(function(error) {
          console.log(error)
      })
      },
    async loadSources(context, payload){
      return await axios
      .post(`${API_BASE_URL}/analytics.getSources`,
      {},
      { params: {
        token: context.state.token,
        club: payload.clubId,
        dateStarts: payload.dateFrom,
        dateEnds: payload.dateTo,
        }
      ,
        headers: 
        {"Content-Type": "application/x-www-form-urlencoded"}
       },
      )
      .then((response) => {
        context.commit('UpdateSourcesInfo',response.data)
      })
      .catch(function(error) {
          console.log('loadSources', error.response.data.error)
          console.log(error.response)
      })
      },

    async loadAvgSchedule(context, payload){
      return await axios
      .post(`${API_BASE_URL}/analytics.avgSchedule`,
      {},
      { params: {
        token: context.state.token,
        club: payload.clubId,
        dateStarts: payload.dateFrom,
        dateEnds: payload.dateTo,
        groupBy: payload.groupBy
        }
      ,
        headers: 
        {"Content-Type": "application/x-www-form-urlencoded"}
       },
      )
      .then((response) => {
        context.commit('UpdateAvgScheduleInfo',response.data)
      })
      .catch(function(error) {
          console.log('loadAvgSchedule', error.response.data.error)
          console.log(error.response)
      })
      },

    async loadAvgIncome(context, payload){
      return await axios
      .post(`${API_BASE_URL}/analytics.avgIncome`,
      {}, 
      { params: {
        token: context.state.token,
        club: payload.clubId,
        dateStarts: payload.dateFrom,
        dateEnds: payload.dateTo,
        groupBy: payload.groupBy
        }
      ,
        headers: 
        {"Content-Type": "application/x-www-form-urlencoded"}
       },
      )
      .then((response) => {
        context.commit('UpdateAvgIncomeInfo',response.data)
      })
      .catch(function(error) {
          console.log('loadAvgIncome', error.response.data.error)
          console.log(error.response)
      })
      },

    async loadConversion(context, payload){
      return await axios
      .post(`${API_BASE_URL}/analytics.getConversion`,
      {},
      { params: {
        token: context.state.token,
        club: payload.clubId,
        dateStarts: payload.dateFrom,
        dateEnds: payload.dateTo,
        groupBy: payload.groupBy,
        }
      ,
        headers: 
        {"Content-Type": "application/x-www-form-urlencoded"}
       },
      )
      .then((response) => {
        context.commit('UpdateConversionInfo',response.data)
      })
      .catch(function(error) {
          console.log(error.response.data.error)
          console.log(error.response)
      })
      },

    async loadMetro(context){
      return await axios
      .post(`${API_BASE_URL}/analytics.metro`,
      {},
      { params: {
        token: context.state.token,
        }
      ,
        headers: 
        {"Content-Type": "application/x-www-form-urlencoded"}
       },
      )
      .then((response) => {
        context.commit('UpdateMetroInfo',response.data)
      })
      .catch(function(error) {
          console.log(error)
      })
      },
    
    
    login({commit}, user) {
        commit('auth_request')
        axios.post(`${API_BASE_URL}/account.auth`,
        {},
        {
          params: {
            login: user.login,
            password: user.password,
            clientId: 1,
          },
          headers: { 'Content-Type': 'application/x-www-form-urlencoded' }
        })
          .then((res) => {
            const token = res.data.access_token;
            localStorage.setItem('token', token);
            commit('auth_success', token, user);
          })
          .catch(() => {
            commit('auth_error');
            localStorage.removeItem('token');
          })
    },

    logout({commit}){
    return new Promise((resolve) => {
      commit('logout')
      localStorage.removeItem('token')
      delete axios.defaults.headers.common['Authorization']
      resolve()
    })
    }
  }
},
)